import React, { Component } from 'react';
import { Table, Input, Button, message } from 'antd';
import { getAction } from '@/axios';

export default class AddOrEditFieldModal extends Component {
    state = {
        columns:[
            {
                title:"",
                dataIndex:"sortIndex"
            },{
                title:"",
                dataIndex:"fieldName",
            },{
                title:"",
                dataIndex:"fieldType"
            }
        ],
        dataSource:[
            // {dataSetFieldId:"",fieldName:"",fieldType:""}
        ],
        filterData:[],
        colName:"",
        id:"",
        fieldName:""
    }
    componentDidMount(){
        this._getFieldsRequest(this.props.dataSet);
        // console.log(this.props.fieldInfo);
        this.setState({
            colName:this.props.fieldInfo.title,
            id:this.props.fieldInfo.dataIndex,
            fieldName:this.props.fieldInfo.fieldName
        })
    }
    //
    _getFieldsRequest = (dataSetId) => {
        getAction(`/ybt/data/dataset/field/list?dataSetId=${dataSetId}`).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState({
                    dataSource:res.obj.map((item,index)=>({...item,sortIndex:index+1})),
                    filterData:res.obj.map((item,index)=>({...item,sortIndex:index+1})),
                })
            }
        })
    }
    _changeColName = (e) => {
        this.setState({colName:e.target.value})
    }
    _changeId = (e) => {
        this.setState({id:e.target.value})
    }
    //
    _search =(val) => {
        let filterData = this.state.dataSource.filter(item=>item.fieldName.indexOf(val)!==-1);
        this.setState({
            filterData
        })
        // console.log(val)
    }
    //
    _save = () => {
        if(this.state.colName===""){
            message.warning("");
            return false;
        }
        if(this.state.id===""){
            message.warning("id");
            return false;
        }
        if(this.state.fieldName===""){
            message.warning("");
            return false;
        }
        try {
            let isRepeat = this.props.tableHead.some(item => {
                return item.dataIndex === this.state.id
            })
            // console.log(this.props.fieldInfo)
            if(JSON.stringify(this.props.fieldInfo)==="{}"){//
                if(isRepeat){
                    message.warning("id");
                    return false;
                    // console.log("id")
                }
            }else{
                if(isRepeat && this.state.id !== this.props.fieldInfo.dataIndex){
                    message.warning("id");
                    return false;
                    // console.log("id")
                }
            } 
        } catch (error) {
            message.error(error)
        }
        let params = {
            title:this.state.colName,
            dataIndex:this.state.id,
            fieldName:this.state.fieldName
        }
        // console.log(params);
        this.props.onOk(params);
    }
    render() {
        let _onRow = (record) => {
            return {
                onClick:e=>{
                    this.setState({fieldName:record.fieldName})
                }
            }
        }
        const labelStyle = {
            color:"#232425",
            width:80,
            textAlign:"right",
            display:"inline-block",
            marginRight:"5px"
        }
        const inputStyle = {
            width:240
        }
        return (
            <React.Fragment>
                <div style={{display:"flex",height:"530px"}}>
                    <div style={{borderRight:"1px solid #E8EBEE",flex:"1",padding:"13px 26px 13px 26px"}}>
                        <div style={{marginBottom:"10px"}}>
                            <label style={labelStyle}></label>
                            <Input style={inputStyle} placeholder="" onChange={this._changeColName} value={this.state.colName}/>
                        </div>
                        <div style={{marginBottom:"10px"}}>
                            <label style={labelStyle}>ID</label>
                            <Input style={inputStyle} placeholder="ID" onChange={this._changeId} value={this.state.id}/>
                        </div>
                        <div style={{marginBottom:"10px"}}>
                            <label style={labelStyle}></label>
                            {
                                this.state.fieldName?
                                <span>{this.state.fieldName}</span>:<span style={{color:"rgba(0,0,0,0.45)"}}></span>
                            }
                            
                        </div>
                    </div>
                    <div style={{flex:"1",padding:"13px 26px 13px 0px",overflow:"hidden",display:"flex",flexDirection:"column"}}>
                        <div>
                            
                            <Input.Search style={{width:250}} placeholder="" onSearch={this._search}/>
                        </div>
                        <Table
                            bordered
                            style={{marginTop:10,overflow:"auto"}}
                            columns={this.state.columns}
                            dataSource={this.state.filterData}
                            rowKey="dataSetFieldId"
                            pagination={false}
                            onRow={_onRow}
                        />
                    </div>
                </div>
                <div style={{textAlign:"right",marginTop:"20px",padding:"10px 20px 20px 20px",borderTop:"1px solid #E8EBEE"}}>
                    <Button type="primary" onClick={this._save} style={{marginRight:"10px"}}></Button>
                    <Button onClick={this.props.onClose}></Button>
                </div>
            </React.Fragment>
            
        )
    }
}
