/**
 * @description: 
 */
import React ,{ Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Button, Input, Select, Modal, Upload, message } from 'antd';
import FieldModal from './addOrEditFieldModal';
import styles from './index.module.less';
import SystemTabs from '../common/systemTabs';
import { getAction, postAction } from '@/axios';

class Add extends Component {
    constructor(props){
        super(props);
        this.state={
            dataSetList:[//
                // {label:"",value:"001"},
            ],
            dataSet:"001",//
            name:"",//
            tableHead:[//
                // {title:"111",dataIndex:"001",fieldName:"tag01"},
            ],
            selectHeadInfo:{},
            fileList:[],//
            showAddFieldModal:false,//
            type:"add",//:/
            editIndex:0,//index
        }
    }
    componentDidMount(){
        this._getDataSetRequest();
    }
    //
    _getDataSetRequest = () => {
        getAction(`/ybt/data/dataset/folder/list?dataSetFolderId=&dataSetName=&dataSetType=2&pageNum=0&pageSize=0`).then(res=>{
            if(res.success){
                this.setState({
                    dataSetList:res.obj.rows.map(item=>({label:item.dataSetName,value:item.dataSetId})),
                    dataSet:res.obj.rows[0]?res.obj.rows[0].dataSetId:""
                })
            }  
        })
    }
    //
    _changeSelect = (value) => {
        Modal.confirm({
            title:"",
            content:"",
            onOk:()=>{
                this.setState({
                    dataSet:value,
                    tableHead:[]
                })
            },
            okText:"",
            cancelText:""
        })
    }
    /// 
    _showFieldModal = (type) => {
        if(type==="add"){
            this.setState({
                showAddFieldModal:true,
                selectHeadInfo:{},
                type
            })
        }else{
            if(JSON.stringify(this.state.selectHeadInfo) === "{}"){
                message.warning("");
                return false;
            }
            this.setState({
                showAddFieldModal:true,
                type
            })
        }
    }
    //
    removeTableHead = () => {
        this.state.tableHead.splice(this.state.editIndex,1)
        this.setState((state)=>({
            tableHead:state.tableHead
        }))
    }
    //
    _selectHead = (record,index) => {
        this.setState({selectHeadInfo:record,editIndex:index})
    }
    //
    _updateName = (e) => {
        this.setState({name:e.target.value})
    }
    //
    _submit = () => {
        if(this.state.tableHead.length===0){
            message.warning("");
            return false
        }
        if(this.state.name===""){
            message.warning("");
            return false
        }
        if(this.state.fileList.length===0){
            message.warning("");
            return false
        }
        console.log(this.state.dataSetList.find(item => item.value===this.state.dataSetId))
        let params = {
            templetName:this.state.name,
            columnConfigJson:this.state.tableHead,
            dataSetId:this.state.dataSet,
            dataSetName:this.state.dataSetList.find(item => item.value===this.state.dataSet).label
        }
        const formData = new FormData();
        formData.append("file",this.state.fileList[0]);
        formData.append("summaryTemplet",JSON.stringify(params).toString());
        postAction("/ybt/summary/save",formData,0,2).then(res=>{
            if(res.success){
                message.success("");
                this.props.history.push("/systemSettings/summaryTemplateManagement")
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _cancle = () => {
        this.props.history.push("/systemSettings/summaryTemplateManagement")
    }
    
    /// ---
    _updateTableHead = (params) => {
        // console.log(params);
        this.setState({showAddFieldModal:false,selectHeadInfo:{}})
        if(this.state.type==="add"){
            this.setState((state)=>({tableHead:[...state.tableHead,params]}))
        }else{
            let tableHead = this.state.tableHead.map((item,index)=>{
                if(index===this.state.editIndex){
                    return params
                }else{
                    return item
                }
            });
            this.setState({tableHead})
        }
    }
    /// 
    _onCancel = () => {
        this.setState({showAddFieldModal:false})
    }
    render() {
        const uploadProps = {
            onRemove: file => {
                this.setState({fileList:[]})
            },
            beforeUpload: file => {
                this.setState({fileList:[file]})
                return false;
            },
            fileList:this.state.fileList
        };
        return (
            <React.Fragment>
                <SystemTabs activeTab="summaryTemplateManagement"/>
                <div className={styles.container}>
                    <div className={`${styles.card} ${styles.addCard}`}>
                        <div className={styles.left}>
                            <div className={styles.top}>
                                
                                <Select onChange={this._changeSelect} value={this.state.dataSet} style={{width:200}}>
                                    {
                                        this.state.dataSetList.map(item=>(<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))
                                    }
                                </Select>
                            </div>
                            <div className={styles.tree}>
                                <div className={styles.buttons}>
                                    <div style={{marginBottom:10,fontWeight:"bold",fontSize:"14px"}}></div>
                                    <Button onClick={()=>{this._showFieldModal("add")}} icon="plus" type="primary" style={{marginRight:10}}></Button>
                                    <Button onClick={()=>{this._showFieldModal("edit")}} style={{marginRight:10}}> </Button>
                                    <Button onClick={this.removeTableHead}> </Button>
                                </div>
                                <div className={styles.headerList}>
                                    {
                                        this.state.tableHead.map((item,index)=>{
                                            return <div onClick={()=>{this._selectHead(item,index)}} className={this.state.selectHeadInfo.dataIndex===item.dataIndex?styles.selected:""} key={item.dataIndex}>{item.title}</div>
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                        <div className={styles.right}>
                            <div className={styles.top} style={{display:"flex"}}>
                                <div style={{padding:"0 10px",lineHeight:"32px"}}></div>
                                <div style={{flex:1}}><Input onChange={this._updateName}/></div>
                            </div>
                            <div className={styles.center}>
                                
                                <div className={styles.table}>
                                    {this.state.tableHead.map(item=>{
                                        return <div key={item.dataIndex}>{item.title}</div>
                                    })}
                                </div>
                            </div>
                            <div className={styles.bottom}>
                                <div>
                                    
                                    <Upload {...uploadProps}>
                                        <Button icon="upload"></Button>
                                    </Upload>
                                    {this.state.fileList.length===0?"":""}
                                </div>
                                <div style={{textAlign:"right"}}>
                                    <Button type="primary" onClick={this._submit}> </Button>
                                    <Button style={{margin:"0 10px"}} onClick={this._cancle}> </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Modal
                    width={800}
                    visible={this.state.showAddFieldModal}
                    title={this.state.type==="add"?"":""}
                    onCancel={this._onCancel}
                    footer={false}
                    destroyOnClose={true}
                    bodyStyle={{padding:"0px"}}
                >
                    <FieldModal 
                        tableHead={this.state.tableHead}
                        dataSet={this.state.dataSet} 
                        fieldInfo={this.state.selectHeadInfo} 
                        onOk={(params)=>{this._updateTableHead(params)}}
                        onClose={this._onCancel}
                    />
                </Modal>
            </React.Fragment>
        )
    }
}


export default withRouter(Add);